import React, { Component, Fragment } from 'react'
import gql from 'graphql-tag'
import { Query } from 'react-apollo'
import { Link } from 'react-router-dom'

import BooleanItem from './BooleanItem'
import Spinner from './Spinner'

const launch_query = gql `query LaunchQuery($flight_number: Int!) {
        launch(flight_number: $flight_number){
            flight_number
            mission_name
            launch_year
            launch_success
            launch_date_local,
            rocket {
                rocket_id
                rocket_name
                rocket_type
            }
        }
    }
`

export class Launch extends Component {
  render() {
    let { flight_number } = this.props.match.params;
    flight_number = parseInt(flight_number);
    return (
      <Fragment>
        <Query query={launch_query} variables={{flight_number}}>
        {
            ({loading, error, data}) => {
                if(loading){
                    return <Spinner />
                } 
                if(error) {
                    console.error(error)
                }

                const {mission_name, launch_year, launch_success, rocket: {rocket_id, rocket_name, rocket_type}} = data.launch;

                return (
                <div>
                    <h1 className="display-4 my-3"><span className="text-dark">Mission:</span>{mission_name}</h1>
                    <h4 className="mb-3">Launch Details:</h4>
                    <ul className="list-group mb-5">
                        <li className="list-group-item">Flight Number: {flight_number}</li>
                        <li className="list-group-item">Launch Year: {launch_year}</li>
                        <li className="list-group-item">Launch Successfull: 
                            <BooleanItem toggle={launch_success}>{launch_success ? "Yes" : "No"}</BooleanItem>
                        </li>
                    </ul>
                    <h4 className="mb-3">Rocket Details:</h4>
                    <ul className="list-group mb-5">
                        <li className="list-group-item">Id: {rocket_id}</li>
                        <li className="list-group-item">Name: {rocket_name}</li>
                        <li className="list-group-item">Type: {rocket_type}</li> 
                    </ul>
                    <Link to="/" className="btn btn-secondary">Back</Link>
                </div>);
            }
        }
        </Query>
      </Fragment>
    )
  }
}

export default Launch
